body {
    color: #505d69;
    background-color: #f1f5f7  !important;
    font-family: 'Inter', sans-serif !important;
}

.table-danger-c {
    background-color: #f6e7e7;
}
.table-primary-c {
    background-color: #e8f3ff;
}
.table-success-c {
    background-color: #e8faec;
}

.side-bar {
    background: #252b3b;
    height: 100vh;
}
.nav-link {
    cursor: pointer;
}

.side-bar .list-group {

}
.side-bar .list-group .list-group-item {
    background-color: inherit !important;
    border: none !important;
}
.side-bar .list-group .list-group-item a {
 color: #8590a5;
 text-decoration: none;
}
.side-bar .list-group .list-group-item a.active {
   color: #d7e4ec !important
}

.font-size-21 {
    font-size: 17px;
}

.c-income {
    color:#108710;
}

.c-expense {
    color: #ff4560;
}

.c-investment {
    color:#008ffb
}

.cp {
    cursor: pointer;
}

.u {
    text-decoration: underline;
}

.tu {
    text-transform: uppercase;
}
.pos-r {
    position: relative;
}
.sidebar-btn {
    display: none;
}

.sidebar-d {
    display: block;
}

.sidebar-m {
    display: none;
}

@media only screen and (max-width: 600px)  {
    .sidebar-btn {
        display: block;
    }
    .side-bar {
        width: 56% !important;
        max-width: 100% !important;;
        position: absolute !important;;
        z-index: 5;
        top: 2rem;
    }
    .sidebar-d {
        display: none;
    }

    .sidebar-m {
        display: block;
    }
    
  }